import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import {  routing } from './app-routing.module';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { LeftmenubarstoreadminComponent } from './leftmenubarstoreadmin/leftmenubarstoreadmin.component';
import { LeftmenubarComponent } from './leftmenubar/leftmenubar.component';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { Ng4LoadingSpinnerModule, Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { AuthGuard } from './guards/auth.guard';
import { AlertComponent } from './directives';
import { FooterComponent } from './footer/footer.component';
import { AuthenticationService } from './services';
import { PrestudentregistrationComponent } from './prestudentregistration/prestudentregistration.component';
import { HomeComponent } from './home/home.component';
import { HeaderComponent } from './header/header.component';
import { StudentService } from './services/student.service';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { PrintService } from './services/print.service';
import { InvoiceComponent } from './invoice/invoice.component';
import { PrintLayoutComponent } from './print-layout/print-layout.component';
import { StudentAdmissionComponent } from './student-admission/student-admission.component';

@NgModule({
  declarations: [
    AlertComponent,
    AppComponent,
    LoginComponent,
    LeftmenubarstoreadminComponent,
    HeaderComponent,
    LeftmenubarComponent,
    FooterComponent,
    PrestudentregistrationComponent,
    HomeComponent,
    InvoiceComponent,
    PrintLayoutComponent,
    StudentAdmissionComponent
   
  ],
  imports: [
    BrowserModule,
    BrowserModule,
     FormsModule,
        HttpClientModule,
       Ng4LoadingSpinnerModule,
        routing,
        NgxDatatableModule
  ],
  providers: [
    AuthGuard,
      ,Ng4LoadingSpinnerService,
      AuthenticationService,
      StudentService,
      PrintService
     
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
