import { Component, OnInit } from '@angular/core';
import { StudentRegistration } from './StudentRegistration-model';
import { NgForm } from '@angular/forms';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { StudentService } from '../services/student.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/interval';
import { PrintService } from '../services/print.service';
declare var $: any;
@Component({
  selector: 'app-prestudentregistration',
  templateUrl: './prestudentregistration.component.html',
  styleUrls: ['./prestudentregistration.component.css']
})
export class PrestudentregistrationComponent implements OnInit {
  template: string =
  `<img src="./assets/images/loader-.png">`
  isLoggedInUser =true;
  showCombination = false;
  todayDate = new Date();
  combination = [];
  bscombination = ["PCM","CBZ","PME","PMCs","CZMb","CBBt"];
  bacombination = ["HES","HEK","PES","JPK","JPE"];
  bvoccombination = ["Infomation Technology","Retails management"];
   model :StudentRegistration= new StudentRegistration();
   showprintdiv= false;
  constructor(private ng4LoadingSpinnerService: Ng4LoadingSpinnerService,
    public printService: PrintService,
    private studentService : StudentService,
    private route: ActivatedRoute,
    private router: Router ) { }

  ngOnInit() {
    this.getFormData();
    this.model.courseName = "-1"
  }
 
  

  onPrintInvoice() {
    const invoiceIds = ['101', '102'];
    this.printService
      .printDocument('invoice', invoiceIds);
  }
  print(){
    this.showprintdiv = true;
    var prtContent = document.getElementById("receipt");
var WinPrint = window.open('', '', 'left=0,top=0,width=800,height=900,toolbar=0,scrollbars=0,status=0');
WinPrint.document.write(prtContent.innerHTML);
WinPrint.document.close();
WinPrint.focus();
WinPrint.print();
WinPrint.close();
this.showprintdiv = false;
  }
  onCourseChange(){
    if(this.model.courseName === "BSC") {
this.showCombination = true;
this.combination = this.bscombination;
    }else {
this.showCombination=false;
    }
  }
  openAddModal() {
   
    
    $("#sucessmodel").modal('show');
    Observable.interval(5000)
    .subscribe((val) => { 
      this.router.navigate(["/"]);
     })

  }
  onSubmitForm(form: NgForm){
console.log(this.model);
this.ng4LoadingSpinnerService.show();
this.studentService.studentRegistration(this.model).subscribe(
  result => {
    console.log(result)
    if(result.status) {
      this.ng4LoadingSpinnerService.hide();
      this.openAddModal();
    /*  Observable.interval(50000)
    .subscribe((val) => { 
      this.router.navigate(["/"]);
     }).unsubscribe(); */
    
    } else {
      this.ng4LoadingSpinnerService.hide();
    }
  },
  error => {
    this.ng4LoadingSpinnerService.hide();
   
  }
)
  }


  
  getFormData(){
    this.studentService.getFormData().subscribe(
      result => {
        console.log(result)
        if(result.status) {
          this.ng4LoadingSpinnerService.hide();
         this.model.applicationNo = result.data.formId;
         this.model.applicationFee = result.data.fre;
        
        } else {
          this.ng4LoadingSpinnerService.hide();
        }
      },
      error => {
        this.ng4LoadingSpinnerService.hide();
       
      })
  }

}
